.forum {
  font-size: 0.3rem;
}
.forum .nav ul:after {
  content: '';
  display: table;
  clear: both;
}
.forum .nav ul li {
  width: 20%;
  line-height: 50px;
  text-align: center;
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid #333;
  color: #333;
}
