  content: '';
  display: table;
  clear: both;
}
.forum .forumlist ul li div {
  float: left;
}
.forum .forumlist ul li div.head {
  width: 1rem;
  height: 1rem;
  padding: 0.2rem;
}
.forum .forumlist ul li div.head img {
  width: 100%;
}
.forum .forumlist ul li div.info {
  width: 5rem;
  overflow: hidden;
  line-height: 0.35rem;
  padding: 0.15rem 0 0 0.3rem;
}
