  line-height: 0.35rem;
  padding: 0 0 0 0.3rem;
}
.forum .forumlist .forumDetail {
  position: absolute;
  top: 0;
  left: 0;
  background: #FFF;
  width: 100%;
  overflow: hidden;
}
.forum .forumlist .forumDetail .ForumDetailHeadbar {
  line-height: 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid #333;
