  content: '';
  display: table;
  clear: both;
}
.forum .forumlist .forumDetail .ForumDetailHeadbar div {
  float: left;
}
.forum .forumlist .forumDetail .ForumDetailHeadbar .back {
  padding: 0 0.3rem;
}
.forum .forumlist .forumDetail .ForumDetailHeadbar .title {
  width: 85%;
  text-align: left;
  text-indent: 0.2rem;
