}
.forum .forumlist .forumDetail .content .main {
  width: 95%;
  margin: 0 auto;
}
.forum .forumlist .forumDetail > .title {
  line-height: 50px;
  font-size: 0.32rem;
  border-top: 1px solid #333;
  margin-top: 0.3rem;
}
.forum .forumlist .forumDetail .commentList {
  width: 100%;
